import $ from "jquery";

class Search {
  constructor() {
    this.resultsDiv = $("#input-overlay__results");
    this.openButton = $(".input-search-trigger");
    this.searchOverlay = $(".input-overlay");
    this.searchField = $("#form1");
    this.startDateField = $("#start-date");
    this.endDateField = $("#end-date");
    this.isOverlayOpen = false;
    this.isSpinnerVisible = false;
    this.previousValue = "";
    this.typingTimer = null;
    this.getResults();
    this.events();
  }

  events() {
    this.openButton.on("click", this.openOverlay.bind(this));
    $(document).on("keydown", this.keyPressDispatcher.bind(this));
    this.searchField.on("keydown", this.typingLogic.bind(this));
    this.startDateField.on("change", this.typingLogic.bind(this));
    this.endDateField.on("change", this.typingLogic.bind(this));
  }

  typingLogic() {
    clearTimeout(this.typingTimer);
    const searchValue = this.searchField.val().trim().toLowerCase();
    const startDate = this.startDateField.val();
    const endDate = this.endDateField.val();

    // if (searchValue) {
    if (searchValue || startDate || endDate) {
      if (!this.isSpinnerVisible) {
        this.resultsDiv.html(
          `<div class="spinner-border text-secondary" role="status" style="max-width:2rem"><span class="sr-only">Loading...</span></div>`
        );
        this.isSpinnerVisible = true;
      }
      this.typingTimer = setTimeout(
        () => this.getResults(searchValue, startDate, endDate),
        2000
      );
    } else {
      // If search field is cleared, display all data again
      this.getResults();
    }

    this.previousValue = searchValue;
  }

  getResults(
    searchQuery = "",
    startDate = "",
    endDate = "",
    pageSize = 8,
    pageNumber = 1
  ) {
    let queryParams = "";

    if (searchQuery || startDate || endDate) {
      const urlSearch =
        "https://data.carinthia.com/api/v4/endpoints/557ea81f-6d65-6476-9e01-d196112514d2?include=image&token=9962098a5f6c6ae8d16ad5aba95afee0";

      if (searchQuery) {
        queryParams += `&search=${encodeURIComponent(searchQuery)}`;
      }
      if (startDate) {
        queryParams += `&startDate=${encodeURIComponent(startDate)}`;
      }
      if (endDate) {
        queryParams += `&endDate=${encodeURIComponent(endDate)}`;
      }

      var apiUrl = urlSearch + decodeURI(queryParams);
    } else {
      const baseUrl =
        "https://data.carinthia.com/api/v4/endpoints/557ea81f-6d65-6476-9e01-d196112514d2";
      const token = "9962098a5f6c6ae8d16ad5aba95afee0";
      queryParams += `?include=image&token=${token}&page[size]=${pageSize}&page[number]=${pageNumber}`;
      var apiUrl = baseUrl + decodeURI(queryParams);
    }

    $.getJSON(
      apiUrl,
      function (data) {
        const filteredData = data["@graph"].filter((item) => {
          let matchesSearch = searchQuery
            ? item.name && item.name.toLowerCase().includes(searchQuery)
            : true;
          let matchesDateRange = true;

          if (startDate || endDate) {
            const itemStartDate = new Date(item.startDate);
            const itemEndDate = item.endDate
              ? new Date(item.endDate)
              : itemStartDate;

            if (startDate) {
              matchesDateRange =
                matchesDateRange && itemEndDate >= new Date(startDate);
            }
            if (endDate) {
              matchesDateRange =
                matchesDateRange && itemStartDate <= new Date(endDate);
            }
          }

          return matchesSearch && matchesDateRange;
        });

        const card = filteredData
          .map((item) => {
            const startDate = item.startDate ? new Date(item.startDate) : null;
            const endDate = item.endDate ? new Date(item.endDate) : null;

            const formatStartDate = startDate
              ? startDate.toISOString().split("T")[0]
              : "Unknown time";
            const formattedEndDate = endDate
              ? endDate.toISOString().split("T")[0]
              : "Unknown time";

            const formattedStartTime = startDate
              ? startDate.toLocaleTimeString("en-US", {
                  hour: "2-digit",
                  minute: "2-digit",
                  hour12: true,
                })
              : "Unknown time";
            const formattedEndTime = endDate
              ? endDate.toLocaleTimeString("en-US", {
                  hour: "2-digit",
                  minute: "2-digit",
                  hour12: true,
                })
              : "Unknown time";

            const location = Array.isArray(item.location) ? item.location : [];
            const locationName =
              location.length > 0
                ? "Location Data Available"
                : "No location provided";
            let location_id =
              location[0] && location[0]["@type"]
                ? location[0]["@type"][1]
                : null;
            location_id = location_id
              ? location_id.replace(/^dcls:/, "")
              : null;
            const desc =
              item.description && item.description.length > 250
                ? item.description.substring(0, 250) + ".."
                : item.description || "No description available";
            return ` 
        <div class="col speaker-item">
            <div class="row">
                <div class="col-lg-6">
                    <div class="si-pic h-100 w-100">
                        <img src="${
                          item.image
                            ? item.image[0].contentUrl
                            : "assets/img/schedule/schedule-2.jpg"
                        }" class="img-fluid h-100 w-100" alt="${item.name}">
                    </div>
                </div>
                <div class="col-lg-6">
                    <div class="si-text">
                        <div class="si-title mb-1">
                            <h4>${item.name}</h4>
                            <span>Speaker</span>
                        </div>

                        ${desc}

                            <div class="si-social mb-0 pt-1">
                                <a href="#">
                                    <i class="fa fa-clock-o"></i>
                                </a>
                                <span>${formatStartDate}</span><br>
                                <span class="pl-5">${formattedEndDate}</span><br>
                                <span class="pl-5">${formattedStartTime}</span><br>
                            </div>
                            <div class="si-social mb-0 pt-1">
                                <a href="#"><i class="fa fa-map-marker"></i></a>
                                <span>${
                                  location_id ? location_id : locationName
                                }</span>
                            </div>
                    </div>
                </div>
            </div>
        </div>
      `;
          })
          .join("");

        this.resultsDiv.html(card);
        this.isSpinnerVisible = false;
      }.bind(this)
    );
  }

  keyPressDispatcher(e) {
    if (
      e.keyCode == 83 &&
      !this.isOverlayOpen &&
      !$("input, textarea").is(":focus")
    ) {
      this.openOverlay();
    }

    if (e.keyCode == 27 && this.isOverlayOpen) {
      this.closeOverlay();
    }
  }

  openOverlay() {
    this.searchOverlay.addClass("input-overlay--active");
    this.isOverlayOpen = true;
  }

  closeOverlay() {
    this.searchOverlay.removeClass("input-overlay--active");
    this.isOverlayOpen = false;
  }
}

export default Search;
